import React, { Component } from "react";
import dynamic from "next/dynamic";
import MetaHead from "../../components/MetaHead";
import WebHeadForWorkPage from "../../components/WebHeadForWorkPage";
import { data } from "../data.json";
import Label from "../../components/Label";
import { GH_PAGE_URL } from "../../constants";
import FacebookComment from "../../components/FacebookComment";
import {
  Layout,
  ArticleSubTitle,
  ArticleTitle,
  Author,
  MainPhoto,
  DateAndLabels,
  LabelsDiv,
  MoreSouceButton
} from "./work-css";
import Sidebar from "../../components/Sidebar";
import FacebookShare from "../../components/FacebookShare";
import Source from "../../components/Source";
import MoreRelation from "../../components/MoreRelation";
import Loading from "../../components/Loading";

class Work extends Component {
  static async getInitialProps(context) {
    const { id } = context.query;
    const articleData = data.find(articleData => articleData.id === id);
    return { articleData };
  }

  render() {
    const {
      title,
      subtitle,
      author,
      date,
      labels,
      url,
      id,
      pointWords
    } = this.props.articleData;
    const href = `${GH_PAGE_URL}/work/${url}`;
    const DynamicWorkingComponent = dynamic(
      () => import(`../../components/Work/${id}`),
      {
        loading: () => <Loading />
      }
    );
    return (
      <div>
        <MetaHead
          title={`${title} ${subtitle} | Ernie Working Note 爾尼工作筆記`}
          description={pointWords}
          url={`https://profile.erniexx.website/work/${url}`}
          ogImage={`/static/img/work/main/${id}.png`}
        />
        <WebHeadForWorkPage />
        <Layout>
          <ArticleTitle>{title}</ArticleTitle>
          <ArticleSubTitle>
            {subtitle}
            <FacebookShare href={href} />
          </ArticleSubTitle>
          <Author>
            <div className="author-image" />
            <div className="author-name-and-labels">
              <p className="author-name">{author}</p>
              <DateAndLabels>
                <span className="date">{date}</span>
              </DateAndLabels>
            </div>
          </Author>
        </Layout>
        <MainPhoto img={id} />
        <Layout ref="element">
          <div style={{ minHeight: "600px" }}>
            {/* <Sidebar articleData={this.props.articleData} /> */}
            <DynamicWorkingComponent />
            <h2>相關資源：</h2>
            <Source />
            {/* <MoreSouceButton>查看更多資源 (1)</MoreSouceButton> */}
            <LabelsDiv>
              {labels.map((label, i) => (
                <Label key={i} name={label.labelName} type={label.labelCode} />
              ))}
            </LabelsDiv>
            <FacebookComment href={href} />
          </div>
        </Layout>
        <MoreRelation />
      </div>
    );
  }
}

export default Work;
